$size: 70px;

@media (max-width: 770px) {
    $size: 60px;
}

.calendar {
    border-collapse: separate;
    border-spacing: 30px;
    
    &__header {
        
        &-row {

        }
        &-item {
            text-align: end;

            &:last-child {
                text-align: start;
            }
        }

        & th {
            text-align: center;
            font-size: 1.815rem;
        }
    }
    &__body {

    }

    &__item {
        position: relative;
        width: $size;
        height: $size;
        border: 1px solid #fff;
        border-radius: 50%;
        padding: 4px;

        &_active {
    
           & a {
               color: #000;
               background-color: #fff;
           } 
        }
    }


    &__item-link {   
        display: flex;
        border-radius: 50%;
        width: 100%;
        height: 100%;
        color: inherit;
    }

    &__item-dayweek, &__item-day {
        position: absolute;
        line-height: 100%;
    }

    &__item-dayweek {
        left: -12px;
        top: -12px;
        color: #fff;
    }

    &__item-day {
        font-size: 2.5rem;
        line-height: 100%;
        padding-bottom: 3px;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);       
    }
}

@media (max-width: 1116px) {
    .calendar {
        margin: 0 auto;
    }
}

@media (max-width: 770px) {
    .calendar {
        
        border-spacing: 20px;
        &__item {
            
        }
    }
}

@media (max-width: 710px) {
    .calendar {
        
        border-spacing: 20px;
        
        &__header {

            & th {
                font-size: 1.5rem;
                padding-bottom: 10px;
            }
        }

        &__item {
            width: 60px;
            height: 60px;
            
            &-dayweek {
                font-size: 0.9rem;
            }
            &-day {
                font-size: 2rem;
            }
        }


    }
}

@media (max-width: 610px) {
    .calendar {
        
        border-spacing: 15px;

        &__item-dayweek {
            left: -10px;
            top: -10px;
            color: #fff;
        }

    }
}

@media (max-width: 580px) {
